<template>
    <div class="container">
      <h1>Reflect on These Questions</h1>
      <div v-for="(question, index) in questions" :key="index">
        <p>{{ question }}</p>
        <textarea v-model="answers[index]" class="text-box" :placeholder="'Your answer to question ' + (index + 1)"></textarea>
      </div>
      <button @click="submitAnswers" class="btn">Submit</button>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        questions: this.$route.params.questions,
        originalText: this.$route.params.originalText,
        answers: ['', '', '']
      }
    },
    methods: {
      async submitAnswers() {
        try {
          const response = await axios.post('/api/generate-speech', {
            originalText: this.originalText,
            questions: this.questions,
            answers: this.answers
          });
          this.$router.push({ name: 'Page3', params: { speech: response.data.speech } });
        } catch (error) {
          console.error('Error:', error);
          alert('An error occurred. Please try again.');
        }
      }
    }
  }
  </script>
  