
<template>
  <div class="container">
    <h1>Share Your Thoughts</h1>
    <textarea v-model="userInput" class="text-box" placeholder="Enter your thoughts here..."></textarea>
    <button @click="submitThoughts" class="btn">Submit</button>
  </div>
</template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      userInput: ''
    }
  },
  methods: {
    async submitThoughts() {
      try {
        const response = await axios.post('/api/generate-questions', { text: this.userInput });
        this.$router.push({ name: 'Page2', params: { questions: response.data.questions, originalText: this.userInput } });
      } catch (error) {
        console.error('Error:', error);
        alert('An error occurred. Please try again.');
      }
    }
  }
}
</script>
